:root {
  --prim-line-grad-start: hsl(236, 72%, 79%);
  --prim-line-grad-end: hsl(237, 63%, 64%);
  --neu-very-light-gray-blue: hsl(240, 78%, 98%);
  --neu-light-gray-blue: hsl(234, 14%, 74%);
  --neu-gray-blue: hsl(233, 13%, 49%);
  --neu-dark-gray-blue: hsl(232, 13%, 33%);
}

:root {
  --font-montserrat: "Montserrat", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-montserrat);
}

body {
  font-size: 15px;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* screen reader only class */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  color: transparent;
}

.card {
  padding: 1.25rem;
}